            <p>
              Exemplo: Nesse cdigo em C,  utilizado ponteiros void para simplificar aritmticas de endereamento. Se cria um ponteiro void a um array de chars para poder simplificar manipulao de seu valor e pular de posio em posio, s atribuindo tipo na hora de dereferenciar o valor no endereo. Aritmtica de ponteiros desse jeito no  possvel em Rust padro, por ser considerado inseguro.
            </p>

