            <p>Exemplo  Tratamento de Excees: Considerando um caso onde desejamos lanar uma exceo quando um nmero que se tenta obter a raiz  negativo.  possvel fazer isso em Rust e em C com enums ou meramente checando o retorno, mas em C  simples de um programador ignorar a possibilidade do retorno indicar erro, pois no h nenhum requisito de que tal erro seja abordado. Por outro lado, Rust possui por padro enum Result, que foi feito para ser utilizado como mecanismo de tratamento de erros e  marcado com a necessidade de ser utilizado, ou seja, se o valor de retorno de uma funo  um Result e ele no  usado em uma expresso ou atribudo em uma varivel, o compilador emite um aviso. </p>

